@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-display: swap;
  font-style: normal;
  font-weight: 300;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
  font-style: normal;
  font-weight: 700;
}
body {
  font-family: "Open Sans", "Arial", sans-serif;
  margin: 0;
  font-size: 14px;
  line-height: 30px;
  position: relative;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
ul {
  list-style: none;
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
.container__inner {
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 660px) {
  .container__inner {
    max-width: 660px;
  }
}
@media (min-width: 960px) {
  .container__inner {
    max-width: 960px;
    padding: 0 10px;
  }
}
.page-header {
  background-color: #283645;
  margin-bottom: -66px;
  z-index: 2;
  position: relative;
}
.page-header .main-nav__toggle {
  position: absolute;
  width: 50px;
  height: 30px;
  border: none;
}
.page-header .main-nav__toggle--close {
  position: absolute;
  width: 50px;
  height: 30px;
  right: 7px;
  top: -54px;
  background-color: #283645;
  outline: none;
  border: none;
}
.page-header .main-nav__toggle--close::before {
  content: "";
  position: absolute;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
  left: 12px;
  bottom: 11px;
  transform: rotate(135deg);
}
.page-header .main-nav__toggle--close::after {
  content: "";
  position: absolute;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
  left: 12px;
  bottom: 11px;
  transform: rotate(45deg);
}
.page-header--close {
  margin-bottom: -70px;
  z-index: 2;
  position: relative;
  background-color: rgba(0, 0, 0, 0.6);
}
.page-header--close .main-nav__toggle {
  position: absolute;
  width: 50px;
  height: 30px;
  right: 20px;
  bottom: 16px;
  border: none;
}
.page-header--close .main-nav__toggle--close::before,
.page-header--close .main-nav__toggle--close::after {
  display: none;
}
.page-header--close .main-nav__toggle--open {
  position: absolute;
  width: 50px;
  height: 30px;
  right: 20px;
  bottom: 21px;
  background: no-repeat url("../img/icon-menu-burger.svg");
  border: none;
}
.page-header--close .main-nav__toggle--open:hover {
  position: absolute;
  width: 50px;
  height: 30px;
  right: 20px;
  bottom: 21px;
  background: no-repeat url("../img/icon-menu-burger-red.svg");
  border: none;
}
.page-header--close .main-nav__toggle--open:active {
  position: absolute;
  width: 50px;
  height: 30px;
  right: 20px;
  bottom: 21px;
  background: no-repeat url("../img/icon-menu-burger-red.svg");
  border: none;
  opacity: 0.3;
}
.page-header--close .site-list {
  display: none;
}
.page-header--no-js {
  background-color: #283645;
  margin-bottom: -66px;
  z-index: 2;
  position: relative;
}
.page-header--no-js .main-nav__toggle {
  display: none;
}
.page-header--no-js .site-list {
  display: block;
}
.page-header__logo {
  display: block;
  width: 170px;
  padding-left: 20px;
  padding-top: 20px;
  padding-bottom: 12px;
}
.page-header__logo-svg {
  fill: #ffffff;
  cursor: pointer;
  width: 80px;
  height: 23px;
}
.page-header__logo-tablet-svg {
  display: none;
}
.page-header__logo-desktop-svg {
  display: none;
}
.page-header__logo-svg:hover {
  fill: #d22856;
}
.page-header__logo-svg:active {
  fill: #ffffff;
  opacity: 0.3;
}
@media (min-width: 660px) {
  .page-header {
    width: 100%;
  }
  .page-header--close {
    min-height: 110px;
  }
  .page-header .main-nav__toggle--close {
    top: -70px;
  }
  .page-header--close .main-nav__toggle--open {
    bottom: 36px;
  }
  .page-header--close .main-nav__toggle--open:hover {
    bottom: 36px;
  }
  .page-header--close .main-nav__toggle--open:active {
    bottom: 36px;
  }
  .page-header__logo-mobile-svg {
    display: none;
  }
  .page-header__logo-desktop-svg {
    display: none;
  }
  .page-header__logo-tablet-svg {
    display: block;
  }
  .page-header__logo {
    display: block;
    padding-left: 20px;
    padding-top: 26px;
    padding-bottom: 29px;
  }
  .page-header,
  .page-header--close,
  .page-header--no-js {
    margin-bottom: -115px;
  }
  .page-header__logo-svg {
    width: 146.3px;
    height: 40.375px;
  }
}
@media (min-width: 960px) {
  .page-header {
    display: flex;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 2;
  }
  .page-header--close {
    display: flex;
    margin-bottom: -116px;
  }
  .page-header__logo-mobile-svg {
    display: none;
  }
  .page-header__logo-tablet-svg {
    display: none;
  }
  .page-header__logo-desktop-svg {
