.main-nav {
  position: relative;
  margin-top: 5px;
}
.main-nav__link {
  color: #ffffff;
}
.main-nav__link:hover:not(.main-nav__link--current) {
  color: #d22856;
  cursor: pointer;
}
.main-nav__link:active:not(.main-nav__link--current) {
  opacity: 0.3;
  color: #d22856;
  cursor: pointer;
}
.main-nav__list {
  text-align: center;
  padding-left: 0;
  margin: 0;
}
.main-nav__toggle--close:hover::before {
  background-color: #d22856;
}
.main-nav__toggle--close:hover::after {
  background-color: #d22856;
}
.main-nav__toggle--close:active::before {
  background-color: #d22856;
  opacity: 0.3;
}
.main-nav__toggle--close:active::after {
  background-color: #d22856;
  opacity: 0.3;
}
