    display: none;
  }
  .main-nav__link:active:not(.main-nav__link--current) {
    opacity: 0.3;
    color: #ffffff;
    cursor: pointer;
  }
}
.site-list {
  position: relative;
}
.site-list::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #000000;
  top: -2px;
  left: 0;
}
.site-list__item {
  border-top: 1px solid #2f4f4f;
